//import scrollTo from 'react-scroll-to-component'

class Nav extends React.Component {

	constructor(props) {
		super(props)

		this.handleClick = this.handleClick;
	}

	componentDidMount() {
		this.scrollTo = require('react-scroll-to-component')
	}

	handleClick (item) {
		console.log('clicked', item)
	}

	scroll() {
		const scrollToComponent = require('react-scroll-to-component');
		console.log('scrolling', this.Skills);
		scrollToComponent(this.Skills, { offset: 0, align: 'middle', duration: 500, ease:'inCirc' });
	}
	
	render () {
		return (
			<nav className="main-nav">
					{this.props.links.map((link, i) => {
						return (
							<a className="main-nav__link"
							key={i}
							href={link.href}
							onClick={this.handleClick.bind(null, link.href)}
							onClick={(event) => {
								event.preventDefault()
								this.handleClick(link.href)
							}}>
								{link.name}
							</a>
						)
					})}
			</nav>
		)
	}

}

export default Nav
